<template>
  <div class="traffic-arbitration">
    <p class="header">{{ $localizeFilter('traffic_arbitration') }}</p>
    <p class="window-title">{{ $localizeFilter('terms_of_cooperation') }}</p>
    <div class="windows-container">
      <div class="window">
        <p class="window-text" v-html="$localizeFilter('traffic_arbitration_text')"></p>
      </div>

      <div class="monetization-channel-windows-container">
        <MonetizationChannelWindow
            v-for="(m,idx) in monetizationChannels"
            :key="idx"
            :idOverride="m.id"
            :telegramNameOverride="m.telegramName"
            :linkOverride="m.link"
            @monetizationChannelAdded="monetizationChannelAdded"
        />
      </div>

    </div>
  </div>
</template>

<script>
import {defineComponent} from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'
import MonetizationChannelWindow from '../components/MonetizationChannelWindow.vue'

export default defineComponent({
  setup() {
  },
  data: () => ({
    monetizationChannels: []
  }),
  components: {MonetizationChannelWindow},
  async mounted() {
    try {
      this.monetizationChannels = await this.$store.dispatch('getMonetizationChannels')
      this.monetizationChannels.push({})
    } catch (e) {
      this.$error(e)
    }
  },
  methods: {
    monetizationChannelAdded(id) {
      this.monetizationChannels[this.monetizationChannels.length - 1].id = id
      this.monetizationChannels.push({})
    }
  }
})
</script>

